import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';

import Overlay from '../../enhance/overlay/index';
import { ActivableRenderer } from '../../enhance/portal/index';

import Button from '../button/Button';
import Icon from '../icon/Icon';

class Modal extends React.Component {
	constructor(props) {
		super(props);
	}

	renderModalHead() {
		return (
			<div className='modal-head'>
				<Button type='button' kind='hollow' shape='circle' skin='default' onClick={this.props.onOverlayClick}><Icon name='close' /></Button>
				<h2 className='modal-head-title'>{this.props.title}</h2>
			</div>
		);
	}

	render() {
		const props = this.props;
		let {
			activated,
			className,
			children,
			onEscKeyDown,
			onOverlayClick,
			onOverlayMouseDown,
			onOverlayMouseMove,
			onOverlayMouseUp,
			...others
		} = props;

		const klassName = KlassName('modal',{
			['activated']:('activated' in props && activated !== false),
		},className);

		return (
			<Overlay
				type='modal'
				activated={activated}
				onClick={onOverlayClick}
				onMouseDown={onOverlayMouseDown}
				onMouseUp={onOverlayMouseUp}
				onMouseMove={onOverlayMouseMove}
				onEscKeyDown={onEscKeyDown}
			>
				<div
					{...others}
					className={klassName}
				>
					{this.renderModalHead()}
					<div className='modal-body'>
						{children}
					</div>
				</div>
			</Overlay>
		);
	}
};

Modal.propTypes = {
	activated: PropTypes.bool,

	title: PropTypes.string.isRequired,
	className: PropTypes.string,
	children: PropTypes.node,

	onEscKeyDown: PropTypes.func,
	onOverlayClick: PropTypes.func,
	onOverlayMouseDown: PropTypes.func,
	onOverlayMouseMove: PropTypes.func,
	onOverlayMouseUp: PropTypes.func,
};

Modal.defaultProps = {
	activated: false,
	title: 'Back',
};

export default ActivableRenderer()(Modal);